
@tailwind base;
@tailwind components;
@tailwind utilities;


/* global styles (if needed) */
body {
  margin: 0;
  padding: 0;
}
/* App.css ya index.css me add karein */
html {
  scroll-behavior: smooth;
}


.sticky-top {
  position: sticky !important;
  top: 0;
  z-index: 50;
}


.App {
  text-align: center;
}



/** Scroll bar remove */
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}
.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

/** welcome to vigyan pathshala   */

@keyframes scrollLeftToRight {
  0% {
    transform: translateX(-5%);
  }
  50% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(-5%);
  }
}

.left-to-right {
  animation: scrollLeftToRight 6s ease-in-out infinite;
  display: inline-block;
}



/*** Empowering mind */
@keyframes bounceX {
  0%, 100% {
    transform: translateX(-3%);
  }
  50% {
    transform: translateX(10%);
  }
}

.animate-bounce-x {
  animation: bounceX 4s ease-in-out infinite;
  display: inline-block;
}



/**  Home page our service section  y axix __/


/* Apply perspective to container */
.perspective-3d {
  perspective: 1000px; /* Gives depth to the container */
}

.card-3d {
  transform-style: preserve-3d;
  transition: transform 1s ease-out;
}

.card-3d.rotate {
  transform: rotateY(0deg);
}

.card-3d:not(.rotate) {
  transform: rotateY(-90deg); /* Start with the card rotated */
  opacity: 0;
}

@keyframes rotateYAnimation {
  from {
    transform: rotateY(-90deg);
    opacity: 0;
  }
  to {
    transform: rotateY(0deg);
    opacity: 1;
  }
}

/* *  Innovation  home page   */
@keyframes fadeIn {
  0% {
    opacity: 0;
    /* transform: translateY(20px); */
    color:#79dfa7
  }
  100% {
    opacity: 1;
    /* transform: translateY(0); */
    /* color: green; */
    
  }
}

.fade-in {
  animation: fadeIn 2s ease-in-out forwards;
}


/* *  Home-1 banner  Most Important  y axix ke according left to right rotate  */
/* Define the Keyframes for rotateYLeft animation */
@keyframes rotateYLeft {
  0% {
    opacity: 0;
    transform: rotateY(-90deg);
  }
  100% {
    opacity: 1;
    transform: rotateY(0deg);
  }
}

.animate-rotateYLeft {
  animation: rotateYLeft 1s ease-out;
   /* animation:'rotateYRight 1s ease-out', */

}
.origin-center {
  transform-origin: center;
}




/** -------Home Page progress bar---- */

@keyframes float {
  0%, 100% {
    transform: rotateX(1deg) rotateY(1deg);
  }
  50% {
    transform: rotateX(3deg) rotateY(-3deg) translateY(-10px);
  }
}

.animated-3d {
  animation: float 2s ease-in-out infinite;
}

/* Fade-in + 3D rotate effect */
.animate-fade-rotate {
  animation: rotateYAnimation 1s ease-out forwards;
}



/* Keyframe animations */
@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}



@keyframes fillProgress {
  0% {
    width: 0%;
  }
  100% {
    width: var(--progress-width);
  }
}

/* Apply animations */
.animate-slide-in {
  animation: slideInFromLeft 1s ease-out forwards;
}

.progress-bar {
  width: 100%;
  background-color: #e0e0e0;
  border-radius: 9999px;
  height: 8px;
}

.progress-fill {
  background-color: #06c5f5;
  border-radius: 9999px;
  height: 8px;
  animation: fillProgress 2s ease-out forwards;
  transition: width 0.3s ease-out; /* Smooth transition */
}




/* About home section me icons  */


@keyframes rotate {
  0% {
    opacity: 0;
    transform: rotateY(0deg);
  }
  0% {
    opacity: 1;
    transform: rotateY(90deg);
  }
  
    100% {
