/* About  */



/** ---Meet our team member about page --------- */
@keyframes infiniteScroll {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-50%);
  }
}

/*! Meet our team member  */
.carousel-wrapper {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.carousel-track {
  animation: infiniteScroll 200s linear infinite;
  display: flex;
  width: max-content;
}

.carousel-card {
  transition: transform 0.3s ease;
}

.carousel-card:hover {
  transform: scale(1.05);
}

.perspective {
  perspective: 1000px;
}

.perspective {
  perspective: 1200px;
}


/*! Carousel end  */




@keyframes float {
  0%, 100% {
    transform: rotateX(4deg) rotateY(4deg) translateY(0);
  }
  50% {
    transform: rotateX(3deg) rotateY(-3deg) translateY(-10px);
  }
}

.animated-3d {
  animation: float 2s ease-in-out infinite;
  transform: rotateX(1deg) rotateY(1deg);
  transform-style: preserve-3d;
